<template>
  <div class="container">
    <h1>Latest Chapters</h1>
    <hr>
    <p class="error" v-if="error">{{ error }}</p>
    <div class="chapters-container">
      <div class="chapter" 
        v-for="(chapter, index) in chapters"
        v-bind:item="chapter"
        v-bind:index="index"
        v-bind:key="chapter._id"
      >
        <p class="text">{{ chapter.text }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import ChapterService from '../ChapterService';

export default {
  name: 'ChapterComponent',
  data() {
    return {
      chapters: [],
      error: '',
      text: '',
    }
  },
  async created() {
    try {
      this.chapters = await ChapterService.getPosts();
    } catch(err) {
      this.error = err.message;
    }
  }
}
</script>

